.memberContainer{
    position: relative;
    width:60%;
    height:auto;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
    margin:25px auto 0 auto;

    &::after{
        content: "";
        position: absolute;
        width:90vw;
        height:2px;
        bottom:-10px;
        background-color: #282E3A; 
    }
}

h2{
    width: 100%;
    text-align: center;
    text-transform: uppercase;
}