.memberProfileContainer{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    flex-basis: 29%;
    padding: 10px;
    margin:10px;
    border:2px solid #e9e9e9;
    flex-grow: 30%;

    img{
        height:auto;
        width:50%;
        border-radius: 10px;
        margin-top: 5px;
    }
}



.name{
    font-weight: 600;
    font-size:1.2em;
    margin: 10px 0 5px 0;
}

